.main-menu {
	visibility: hidden;
	position: fixed;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
	
	.logo-type {
		width: 100%;
		margin-bottom: 30px;
		img {
			width: 100%;
		}
	}
	&::after {
		content: '';
		position: absolute;
		left: 0;
		top: 0;
		right: 0;
		bottom: 0;
		background-color: rgba(0,0,0, .7);
		opacity: 0;
		transition-property: opacity;
		transition-duration: 500ms;
		z-index: 99;
	}
}
.main-menu__list {
	list-style: none;
	position: relative;
	z-index: 200;
	max-width: 320px;
	height: 100%;
	margin: 0;
	box-sizing: border-box;
	padding: 32px 20px 0 32px; 
	background-color: white;
	transform: translateX(-100%);
	transition-duration: 500ms;
	transition-property: transform;
	overflow-y: auto;
}
.main-menu__item {
	margin: 20px 0;
	button {
		box-shadow: none;
	}
	
}
@media screen and (min-width: 100px) { 
	.main-menu__item {
	margin: 15px 0;
	button {
		display: block;
		box-shadow: none;
	}
}
}
.main-menu__link, .main-menu__close {
	font-size: 14px;
	line-height: 16px;
	font-weight: bold;
	text-decoration: none;
	color: $black;
	border: none;
	text-align: left;
	width: 100%;

	&:hover {
		text-decoration: underline;
	}
}
.main-menu__link {
	color: #0059A5;

}

.main-menu__close {
	padding-left: 40px;
	background: url('../img/icon/close.svg') left center no-repeat;
	margin-bottom: 20px;
}
.main-menu--active {
	z-index: 200;
	visibility: visible;
	&::after {
		opacity: 1;
		// display: none;
}
	.main-menu__list {
		transform: translateX(0);
	}
}