.fixed {
	padding-bottom: d-font(10,18,$min_width,$max_width);
	position: fixed;
	top: 0;
	left: 50%;
	transform: translateX(-50%);
	z-index: 10;
	color: $blue;
	box-sizing: border-box;
	background-color: $white;
	width: 100%;
	z-index: 50;
	box-shadow: 0px 3px 3px rgba(0, 0, 0, 0.15);
}
.empty_block {
	height: d-font(20,62,$min_width,$max_width);
	max-height: 62px;
}
.fixed__wrap {
	display: flex;
	align-items: center;
	margin-top: 10px;
	img {
			width: d-font(100,350,$min_width,$max_width);
		}
}
.fixed__hamburger {
	flex-basis: 18px;
	flex-shrink: 0;
	cursor: pointer;
	margin-right: 5%;
	span {
		display: block;
		background-color: $blue;
		height: 2px;
		border-radius: 4px;
		width: 100%;
		margin-bottom: 2px;
		&:nth-child(3) {
			margin-bottom: 0;
			width: 75%;
			margin-left: auto;
		}

	}
	
}
.info-content {
		margin: auto;
		
		padding: d-font(3,10,$min_width,$max_width);
		p {
			font-size: d-font(6,24,$min_width,$max_width);
			line-height: d-font(8,24,$min_width,$max_width);
			&:nth-child(1) {
				margin-bottom: d-font(2,10,$min_width,$max_width) !important;
				padding-left: 11px !important;
			}
			&:nth-child(2) {
				font-size: d-font(6,24,$min_width,$max_width);
				line-height: d-font(10,24,$min_width,$max_width);
				padding-left: d-font(11,24,$min_width,$max_width);
				background: url("../img/phone.svg") left center no-repeat;
				background-size: d-font(7,20,$min_width,$max_width);

			}
		
		
		}

}
button {
	
	font-size: d-font(7,24,$min_width,$max_width);
	height: d-font(16,59,$min_width,$max_width);
	width: d-font(78,307,$min_width,$max_width);
	color: $blue;
	background: #FFFFFF;
	border: d-font(1,2,$min_width,$max_width) solid #6959B6;
	box-sizing: border-box;
	box-shadow: 0px 3px 3px rgba(0, 0, 0, 0.15);
	border-radius: 30px;
}
