.programm_block {
	
	.title_programm {
		width: 100%;
		padding-top: 20px;
		padding-bottom: 20px;
		display: flex;
		align-items: center;
		text-align: center;
		text-transform: uppercase;
		justify-content: center;
		h3{
			margin: 20px 0 20px 0;
			color: #6959B6;
			font-style: normal;
			font-weight: bold;
			font-size: d-font(18,36,$min_width,$max_width);
			line-height: 100%;
		}
	}
	.image_block {
		width: 100%;
		img {
			width: 100%;
		}
	}
	.group_programm {
		min-height: 100px;
		margin-bottom: 25px;
	}
	.list_programm {

		margin-right: auto;
		margin-left: auto;
		margin-bottom: 10px;
		width: 90%;
		background: linear-gradient(304.65deg, #EBEBED 39.31%, rgba(235, 235, 237, 0) 74.68%);
		display: flex;
		flex-direction: column;
		.name_programm {
			text-align: center;
			margin-bottom: 25px;
			h4 {
				margin-top: 0px;
				margin-bottom: 5px;
				font-style: normal;
				font-weight: bold;
				font-size: d-font(14,25,$min_width,$max_width);
				line-height: 120%;
				color: #0059A5;
				text-transform: uppercase;
				span {
					margin-left: 5px;
					color: black;
				}
			}
		}
		.next__content {
			display: flex;
			flex-direction: column;
		}
		.next_text_block {
			padding-top: 24px;
			padding-bottom: 20px;

			p {
				font-size: d-font(12,18,$min_width,$max_width);
				line-height: 130%;
			}
		}
		button {
			align-self: flex-end;
				margin-left: auto;
				margin-right:auto;
				
			display: block;
			align-items: center;
			justify-content: center;
			font-size: px-to-rem(16px);
			height: 40px;
			width: 130px;
			color: #393185;
			background: #FFFFFF;
			/* Actionable Item */

			border: 1px solid #393185;
			box-sizing: border-box;
			border-radius: 20px;
			}
		
	}
	
}
@media screen and (min-width: 440px) {
	.programm_block {
		

		.next__content {
			display: flex;
			flex-direction: row;
		}
		
		background: linear-gradient(304.65deg, #EBEBED 39.31%, rgba(235, 235, 237, 0) 74.68%);
		.image_block{
			display: none;
		}
		
		.list_programm {
			margin-left: auto;
			margin-right: auto;
			background:none;
			max-width: 600px;
			button {
				margin-right: 0;
			}
		}
		.name_programm {
			text-align: left !important;
		}
	}
}

@media screen and (min-width: 1239px) {
	.programm_block {
		background-image: url('../img/bg_program.png');
		background-repeat: no-repeat;
		background-position: right;
		background-size: contain;
		padding-bottom: 20%;
		.title_programm {
			width: auto;
			justify-content: start;
			
			
		}
		.list_programm {
			margin-left: 10%;
			max-width: 650px;}
			.next_text_block {
				padding-bottom: 0 !important;
			}
		.name_programm {
			h4{
				margin-bottom: 15px;
			}
		}
	}
}
@media screen and (min-width: 1800px) {
	.programm_block {
		.list_programm {
			margin-left: 10%;
			max-width: 750px;}
	}
}