.course_info {
	.title_course_info{
		width: 100%;
		padding-top: 20px;
		padding-bottom: 10px;
		display: flex;
		align-items: center;
		text-align: center;
		text-transform: uppercase;
		justify-content: center;
		h3 {
			color: #0059A5;
				font-style: normal;
				font-weight: bold;
				font-size: d-font(18,36,$min_width,$max_width);
				line-height: 120%;
		}
	}
	.block_info{
		display: flex;
		flex-direction: column;
		align-items: center;
		.card_info{
			padding: 10px;
			margin-bottom: 2%;
			display: flex;
			flex-direction: column;
			align-items: center;
			max-width: 630px;
			.image_info{
				width: 100%;
				text-align: center;
				img {
					width: 75%;
					max-width: 500px;
					
				}
			}
			.description_info{
				width: 90%;
				padding-left: 30px;
				padding-right: 30px;
				padding-bottom: 10px;
				h3 {
					font-style: normal;
					font-weight: bold;
					font-size: d-font(16,28,$min_width,$max_width);
					line-height: 110%;
					text-align: center;
				}
				p{
					font-style: normal;
					font-weight: normal;
					font-size: d-font(11,25,$min_width,$max_width);
					line-height: 145%;
					text-align: center;
					margin-bottom: 1rem;
				}
			}
		}
	}
}

@media screen and (min-width: 670px) {
	.block_info {
		.card_info {
			box-shadow: 3px 3px 10px rgba(0,0,0,.5)!important;

		&:nth-child(1){
			background: linear-gradient(311.84deg, #EEEEEE 21.2%, #F2F2F2 21.21%, #FCFCFC 42.48%, #FFFFFF 70.85%);
		}
		&:nth-child(2){
			background: linear-gradient(108.41deg, #EEEEEE 14.71%, #FFFFFF 62.49%);
		}
		&:nth-child(3){
			background: linear-gradient(291.65deg, #EEEEEE 37.35%, #FFFFFF 66.8%);
		}
		&:nth-child(4){
			background: linear-gradient(102.13deg, #EEEEEE 13.99%, #FFFFFF 61.87%);
		}
	}
	}
}
@media screen and (min-width: 900px) {
	.block_info {
	.card_info{
		flex-direction: row !important;
		min-height: 400px !important;
		width: 80% !important;
		max-width: 80% !important;
		.image_info{
			width: 100% !important;
			max-width: 45.5rem;
		}
		.description_info{
			h3 {text-align: left !important;}
			p {text-align: left !important;}
			max-width: 760px;
		}
		&:nth-child(2){
			flex-direction: row-reverse !important;
		}
		&:nth-child(4){
			flex-direction: row-reverse !important;
		}
		}
	}
}