@charset "UTF-8";

/**
 * Основные стили
 **/

html {
	font-size: 16px;
	color: $black;
}

body {
	font-family: 'Noto Sans', sans-serif;
	overflow-wrap: break-word;
	word-wrap: break-word;
	
}

.container__div {
	@include min-max(320px, 100%, 0);
}

.wrap {
	@include min-max(320px, 1340px, 0px);
}

p {
	margin: 0;
}

button:active, button:focus {
  outline: none;
}
button::-moz-focus-inner {
  border: 0;
}
input::-moz-focus-inner {
  border: 0;
}
input:active, input:focus {
  outline: none;
}
// * {box-sizing: border-box;}

.container__box {
	width: d-font(310,1344,$min_width,$max_width);
	margin-right: auto;
	margin-left: auto;

}


.light-btn {
    position: relative;
    overflow: hidden;
}
.light-btn::after {
    content: "";
    display: block;
    width: 30px;
    height: 180px;
    margin-left: 60px;
    background: linear-gradient(to left, rgba(255,255,255,.0), rgba(255,255,255,.8), rgba(255,255,255,.0)) no-repeat;
    position: absolute;
    left: -40px;
    top: -60px;
    z-index: 1;
    -webkit-transform: rotate(125deg);
    transform: rotate(125deg);
    -webkit-transition: all 0.1s;
    transition: all 0.1s;
    -webkit-animation-name: slideme;
    animation-name: slideme;
    -webkit-animation-duration: 3s;
    animation-duration: 3s;
    -webkit-animation-delay: 0.05s;
    animation-delay: 0.05s;
    -webkit-animation-timing-function: ease-in-out;
    animation-timing-function: ease-in-out;
    -webkit-animation-iteration-count: infinite;
    animation-iteration-count: infinite;
}

@-webkit-keyframes slideme {
    0% { left: -30px; margin-left: 0px; }
    30% { left: 110%; margin-left: 80px; }
    100% { left: 110%; margin-left: 80px; } 
}

a {
    text-decoration: none;
    cursor: pointer;
}

