.block__payment {
	background: linear-gradient(275.36deg, #D4DBE7 19.6%, rgba(196, 196, 196, 0) 100.02%);
	padding: 2% 10% 15% 10%;
	.title__payment {
		display: flex;
		flex-direction: column;
		align-items: center;
		padding-bottom: 5%;
		h3{
			font-style: normal;
			font-weight: bold;
			font-size: d-font(18,36,$min_width,$max_width);
			line-height: 120%;
			display: flex;
			align-items: center;
			text-align: center;
			letter-spacing: -0.015em;
			text-transform: uppercase;
			color: #0059A5;
		}
	}
	.payment__block {
		display: flex;
		flex-direction: column;
		align-items: center;
		.card__pay {
			padding: 10px;
			margin-bottom: 20px;
			background: #FFFFFF;
			box-shadow: 4px 4px 15px 2px rgba(0, 0, 0, 0.25);
			border-radius: 15px;
			width: d-font(123,265,$min_width,$max_width);
			.image__pay {
				width: 100%;
				img {
					width: 100%;
				}
			}
			.description__pay {
				p {
					font-size: d-font(9,14,$min_width,$max_width);
					line-height: 24px;
					/* or 171% */	
					font-weight: bold;
					text-align: center;
					letter-spacing: -0.015em;
					text-transform: uppercase;

					color: #000000;
				}
			}
		}
	}
}
@media screen and (min-width: 400px) {
	.payment__block {
		display: flex;
		flex-direction: row !important;
		flex-wrap: wrap;
		align-items: center;
		justify-content: space-between;
	}
}