.price_block {
	display: flex;
	flex-direction: column;
	align-items: center;
	.title_price {
		width: 100%;
		padding-top: 20px;
		padding-bottom: 20px;
		display: flex;
		align-items: center;
		text-align: center;
		text-transform: uppercase;
		justify-content: center;
		h3{
			color: #6959B6;
			font-style: normal;
			font-weight: bold;
			font-size: d-font(18,36,$min_width,$max_width);
			line-height: 120%;
		}
	}
	.name_price {
		margin: 0 auto 20px auto;
		width: 100%;
		max-width: 400px;
		
		.title_price {
			display: flex;
			align-items: center;
			justify-content: center;
			background: radial-gradient(50% 50% at 50% 50%, #FFF0A3 48.44%, #FFC700 100%);
			h1{
				font-style: normal;
				font-weight: bold;
				font-size: d-font(38,64,$min_width,$max_width);
				line-height: 100%;
				display: flex;
				align-items: center;
				text-align: center;
				color: #E08401;
			}
		}
		.description {
			background: linear-gradient(214.27deg, #DADADA 20.55%, #FFFFFF 53.29%);
			border-radius: 0px 0px 8px 8px;
			display: flex;
			flex-direction: column;
			align-items: center;
			padding: 10%;
			text-align: center;
			.hidden_description_block {
				display: none;
			}
			a {
				margin-bottom: 5%;
				font-style: normal;
				font-weight: bold;
				font-size: 18px;
				line-height: 100%;
				display: flex;
				align-items: center;
				text-align: center;
				text-transform: uppercase;
				color: #6959B6;
				text-decoration: none;
			}
			hr {
				width: 100%;
				border: none;
				background-color: #000000;
				height: 1px;
			}
			p {
				justify-content: center;
				font-style: normal;
				font-weight: normal;
				font-size: d-font(14,24,$min_width,$max_width);
				line-height: 130%;
				display: flex;
				align-items: center;
				text-align: center;
				color: #000000;
			}
			.old_price {
				.price_rub p{
					text-decoration-line: line-through;
				}
			}
			.new_price {
				.price_rub p{
					font-weight: bold;

				}
			}
			button {
				margin: 13px auto 8px auto;
				display: block;
				align-items: center;
				justify-content: center;
				font-size: d-font(15,24,$min_width,$max_width);
				background: radial-gradient(50% 50% at 50% 50%, #FFF0A3 48.44%, #FFC700 100%);
				border-radius: 70px;
				box-sizing: border-box;
				box-shadow: none;
				height: d-font(40,55,$min_width,$max_width);;

				width: 100%;
				max-width: 320px;
				color: rgba(227, 89, 30, 0.66);
				font-weight: bold;
				border: none;
			}

		}
	}
}

@media screen and (min-width: 700px) {
	.list_price_block {
		display: flex;
		flex-direction: row;
		justify-content: center;
		flex-wrap: wrap;
		width: 80%;
	}
}

@media screen and (min-width: 1332px) {
	.list_price_block {
		width: 100%;
		margin: 0 auto 0 auto;

		.name_price {
			width: 100%;
			max-width: 440px;
			margin: 0;
			margin-right: 1%;
			.description {

				.hidden_description_block {
					min-height: 700px;
					display: flex;
					flex-direction: column;
					justify-content: start;
					align-items: flex-start;
					justify-self: start;

					p{	
						background-image: url('../img/icon/icon_star.svg');
						background-repeat: no-repeat;
						background-size: 28px;
						background-position: top left;
						padding-left: 35px;
						padding-top: 5px;
						align-items: flex-start;
						text-align: left;
						font-size: 18px;
						font-style: normal;
						font-weight: normal;
						line-height: 100%;
						min-height: 50px;

					}
					.red {

							color:  #FF0D0D
						
					}
					.muted {
						color: #404040;
					}
					
				}
				a {
					display: none;
				}
			}
		}
		
	}
}