.couch_block {
	padding: 17px 21px 35px 21px;
	.title_couch {
		width: 100%;
		display: flex;
		align-items: center;
		text-align: center;
		h3 {
			width: 100%;
			font-style: normal;
			font-weight: bold;
			font-size: d-font(18,36,$min_width,$max_width);
			line-height: 120%;
			text-transform: uppercase;
			color: #0059A5;
		}
	}
	.description_couch {
		display: flex;
		flex-direction: column;
		align-items: center;
	}
	.image_couch_block {
		width: 100%;
		max-width: 333px;
		img {
			width: 100%;
		}
		margin-bottom: 20px;
	}
	.text_couch_block {
		height: 100%;
		width: 100%;
		max-width: 657px;
		font-style: normal;
		font-weight: normal;
		font-size: d-font(14,20,$min_width,$max_width);
		line-height: 110%;
		color: rgba(0, 0, 0, 0.8);
		p {
			margin-bottom: 3.5%;
		}
	}
	
}
@media screen and (min-width: 1260px) {
	.couch_block {
		background: linear-gradient(293.35deg, #DEDEDE 14.16%, rgba(222, 222, 222, 0) 63.76%);
	
	}
	.description_couch {
		margin-left: auto;
		margin-right: auto;
		width: 80%;
		display: flex;
		flex-direction: row-reverse !important;
		align-items: stretch !important;
		height: 100%;
		justify-content: center;
		.image_couch_block {
			max-width: 600px;
			margin-bottom: 0;
		}
		.text_couch_block {
			margin-right: 2%;
			display: flex;
			flex-direction: column !important;
			align-items: stretch !important;
			height: auto;
			justify-content: space-around;
			p {
				width: 100%;
				margin: 0;
			}
		}
	}
}