.faq_block {
	box-sizing:border-box;
	padding: 26px 10% 32px 10%;
	box-shadow: 0px 3px 3px rgba(0, 0, 0, 0.15);
	.title_faq {
		width: 100%;
		display: flex;
		align-items: center;
		text-align: center;
		h3 {
			width: 100%;
			font-style: normal;
			font-weight: bold;
			font-size: d-font(18,36,$min_width,$max_width);
			line-height: 120%;
			text-transform: uppercase;
			color: #0059A5;
		}
	}
	.faq_item {
		background: radial-gradient(79.11% 79.11% at 50% 50%, #FFFFFF 42.71%, #F0F0F0 100%);
		border: 2px solid #6959B6;
		box-sizing: border-box;
		box-shadow: 4px 4px 10px rgba(0, 0, 0, 0.25);
		border-radius: 5px;
		margin-bottom: 15px;
		min-height: 103px;
		padding: 3%;
		font-style: normal;
		font-size: d-font(12,24,$min_width,$max_width);
		line-height: 130%;
		text-align: center;
		color: rgba(0, 0, 0, 0.8);
		display: flex;
		flex-direction: column;
		justify-content: space-around;
		.question {
			font-weight: bold;
			margin-bottom: 3%;
		}
	}
}

@media screen and (min-width: 1260px) {
	.faq_block{
		background: linear-gradient(293.35deg, #DEDEDE 14.16%, rgba(222, 222, 222, 0) 63.76%);	
		padding-left: 7%;
		padding-right: 7%;
	}
	.faq_list {
		display: flex;
		flex-direction: row;
		flex-wrap: wrap;
		justify-content: space-around;
		.faq_item{
			flex:1 0 calc(33.33% - 30px); 
			margin:3%;
			box-sizing:border-box;
			width: 590px;
			max-width: 590px;
			background: none;
			background-color: white;
		}
	}
}