.img__whats_app_up {
	display: none;
}

.form_questions {
	
	display: flex;
	flex-direction: column;
	align-items: center;
	margin-bottom: 60px;
	.block__dynamic {
		display: flex;
		flex-direction: column;
		align-items: center;
		
	.question__footer {
		display: flex;
		flex-direction: column;
		align-items: center;
		margin-bottom: 24px;
		max-width: 430px;
		.img__whats_app_up  a {
			margin-top: 25px;
			display: flex;
			flex-direction: row;
			align-items: center;
			text-decoration: none;
			cursor: pointer;
			p {
				font-style: normal;
			font-weight: normal;
			font-size: 18px;
			line-height: 14px;
			color: #0059A5;
			display: flex;
			align-items: center;
			text-align: center;
			letter-spacing: -0.015em;
			margin-left: 11px;
			}
			img {
				width: 35px;
				height: 35px;
			}
			

			
		}
	}
	.title_form {
		font-style: normal;
		font-weight: bold;
		font-size: d-font(18,30,$min_width,$max_width);
		line-height: 110%;
		display: flex;
		align-items: center;
		text-align: center;
		color: #6959B6;
		h3 {width: 100%;} width: 100%;
			// margin-bottom: 15px;
		}
	.sub_title_question {
		font-style: normal;
		font-weight: normal;
		font-size: d-font(14,18,$min_width,$max_width);
		line-height: 130%;
		display: flex;
		align-items: center;
		text-align: center;
		color: #0059A5;
		justify-content: center;
		width: 80%;
		p {width: 100%;}
	}
	.phone__number_questions {
		font-style: normal;
		font-weight: bold;
		font-size: 24px;
		line-height: 50px;
		/* or 208% */

		letter-spacing: -0.015em;

		color: #393185;
		h3 {
			margin-bottom: 0 !important;
		}
		
	}
	.image__question_man {
		display: flex;
		flex-direction: row;
		img {
		height: d-font(352,439,$min_width,$max_width) !important;
		width: d-font(187,233,$min_width,$max_width) !important; 
}
		a {
			cursor: pointer;
			text-decoration: none;
		}
		.img__left {

		}
		.img__whats_app a {
			margin-top: 30px;
			display: flex;
			flex-direction: column;
			align-items: center;
			width: 90px;
			img {
				width: 63px !important;
				height: 63px !important;
				margin-bottom: 15px
			}
			p {
				font-weight: normal;
				font-size: 16px;
				line-height: 130%;
				/* or 78% */

				display: flex;
				align-items: center;
				text-align: center;
				letter-spacing: -0.015em;

				color: #0059A5;
			}
		}
	}
}
	form {
		box-sizing:border-box;
		max-width: 430px;
		width: 90%;
		background: #E1E1E1;
		box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
		border-radius: 4px;
		padding: 25px;
		display: flex;
		flex-direction: column;
		align-items: center;
		margin-bottom: 30px;
		.title {
			font-style: normal;
			font-weight: 500;
			font-size: 14px;
			line-height: 17px;
			display: flex;
			align-items: center;
			text-align: center;
			color: #0059A5;
			margin-bottom: 15px;
			h4 {margin: 0;}
		}
		input {
			box-sizing:border-box;
			margin-bottom: 20px;
			border: none;
			min-height: 36px;
			width: 100%;
			font-style: normal;
			font-weight: 500;
			font-size: 14px;
			line-height: 110%;
			display: flex;
			align-items: center;
			border-radius: 5px;
			filter: drop-shadow(2px 2px 6px rgba(0, 0, 0, 0.2));
			color: #000000;
			padding: 10px;
		}
		button {
			box-sizing:border-box;
			justify-content: center;
			background: #6959B6;
			border-radius: 30px;
			font-style: normal;
			font-weight: bold;
			font-size: d-font(14,17,$min_width,$max_width);
			line-height: 100%;
			display: flex;
			align-items: center;
			text-align: center;
			color: #FFFFFF;
			min-height: 34px;
			min-width: 167px;
			margin-bottom: 21px;
		}
		p:last-child {
			font-style: normal;
			font-weight: 500;
			font-size: 12px;
			line-height: 15px;
			display: flex;
			align-items: center;
			text-align: center;
			letter-spacing: -0.015em;

			color: #727272;
		}
	}
}

@media screen and (min-width: 1093px) {
	.form_questions {
		padding-left: 10%;
		padding-right: 10%;
	}
	.img__whats_app {
		display: none;
	}
	.img__whats_app_up {
		display: block !important;
	}
	.block__dynamic {
		flex-direction: row-reverse !important;
		width: 100%;
		justify-content: space-around;
	}
	.question__footer {
		text-emphasis: left;
		align-items: flex-start !important;
		padding: 10px;
		width: 50%;
	}
	.image__question_man {
		align-self: flex-end;
		img {
			// max-height: 439px;
			// max-width: 233px;

		}
	}
	.form_questions {
		min-height: 481px;
		display: flex;
		flex-direction: row;
		align-items: stretch;
		justify-content: space-evenly;
		background: linear-gradient(4.32deg, #D5D5D5 4.57%, rgba(245, 245, 245, 0) 71.06%);
		.question__footer {
			max-width: 168px;
			width: 80%;
			.title_form {
				text-align: left;
			}
			.sub_title_question {
				width: 100%;
				text-align: left !important;
			}
		}
		form {
			margin-top: 30px;
			justify-content: space-around;
		}
	}
}

