// Невидимые блоки
.list__image {
	display: none;
}
.hidden__text__image{
	display: none;
}

.header {
}

.header__title {
	width: 100%;
	background-color: $greyfon;
	
	.container__box {
		
	}
}
.header__info {
	width: 100%;
}

.header__text {
	padding-top: 10px;
	display: flex;
	flex-direction: row;
	align-items: center;
	justify-content: space-between;
	text-transform: uppercase;
	color: $white;
	font-style: normal;
	font-weight: normal;
	.text__left {
		position: relative;
		align-self: center;
		width: d-font(138,315.75,$min_width,$max_width);
		height: d-font(85,260,$min_width,$max_width);
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		z-index: 10;
		margin-right: d-font(10,25,$min_width,$max_width);
		.image__ellipse {
			position: absolute;
			z-index: 0;
			img {
				width: d-font(148,315.75,$min_width,$max_width);
				height: d-font(128,280,$min_width,$max_width);
			}
		}
		p {
			z-index: 3;
			span {
					font-size: d-font(12,64,$min_width,$max_width);
					line-height: 110%;
			}
			
			&:nth-child(1) {
				font-size: d-font(8,24,$min_width,$max_width);
				line-height: 100%;
				font-weight: normal;
			}
			&:nth-child(2) {
				font-weight: 800;
				font-size: d-font(12,36,$min_width,$max_width);
				line-height: 100%;

			}
		}
	}
	.text__right {
		p {
			position: relative;
			z-index: 30;
		}
		width: 100%;
		align-self: center;
		width: 100%;
		margin-left: px-to-rem(10px);
		font-style: normal;
		font-weight: bold;
		font-size: d-font(25,115,$min_width,$max_width);
		line-height: d-font(28,115,$min_width,$max_width);
		color: $white;
		text-shadow: 3px 3px 3px rgba(0, 0, 0, 0.25);
		padding-left: 20px;
		margin-top: 20px;
		.info-title-block {
			font-size: d-font(8,26,$min_width,$max_width);
			line-height: 220%; 
			font-weight: normal;
			color: #0059A5;
			text-shadow: none;
			padding-left: d-font(10,30,$min_width,$max_width);
			background: url("../img/star.svg") left center no-repeat;
			background-size: d-font(7,20,$min_width,$max_width);
		}
	}
}

.header__image {
	height: auto;
	width: 100%;
	background-color: $greyfon;
	text-align: right;
	img {
		max-width: 965px; 
		width: d-font(300,1120,$min_width,$max_width);
		
	}
}
.header__info{
	display: flex;
	flex-direction: column;
	background-color: $white;

}
.avtor__info {
	width: 70%;
	height: 55px;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-self: flex-end;
	clip-path: polygon(35px 0%, 100% 0%, 100% 100%, 0% 100%);
	background: #F3F6FC;
	box-shadow: 9px 6px 6px rgba(0, 0, 0, 0.25);
	margin-top: -20px;
	margin-bottom: 30px;
	margin-left: auto;
	padding-left: 60px;
	padding-top: auto;
	padding-bottom: auto;
	font-style: normal;
	font-size: px-to-rem(14px);
	line-height: px-to-rem(16px);
	color: $blue;
	text-transform: uppercase;
	p {
			&:nth-child(1) {
				font-weight: 300;
			}
			&:nth-child(2) {
				font-weight: 800;
				
			}
		}
}

.list__info {
	margin-left: auto;
	margin-right: auto;
	margin-bottom: 20px;
	font-style: normal;
	font-weight: 600;
	font-size: d-font(16,24,$min_width,$max_width);
	line-height: d-font(29,33,$min_width,$max_width);
	color: $greytext;
	p {
		margin-bottom: 10px;
		padding-left: 38px;
		background: url("../img/ok.svg") left center no-repeat; 
	}
	
}
.program__info {
	width: 70%;
	height: 55px;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-self: flex-start;

	clip-path: polygon(0% 0%, calc(100% - 35px) 0%, 100% 100%, 0% 100%);
	background: #F3F6FC;
	box-shadow: 9px 6px 6px rgba(0, 0, 0, 0.25);
	padding-left: 60px;
	font-style: normal;
	font-size: px-to-rem(14px);
	line-height: px-to-rem(16px);
	color: $blue;
	text-transform: uppercase;
	box-shadow: 9px 6px 6px rgba(0, 0, 0, 0.25);
	p {
		margin-left: auto;
		margin-right: 80px;
	}
}
.shadow {
  filter: drop-shadow(-1px 6px 3px rgba(50, 50, 0, 0.5));
}


@media screen and (min-width: 400px) {
		.text__right {
		p {
			position: relative;
			z-index: 30;
		}
		width: 100%;
		align-self: center;
		width: 100%;
		margin-left: px-to-rem(10px);
		font-style: normal;
		font-weight: bold;
		font-size: d-font(19,115,$min_width,$max_width) !important;
	}
}

@media screen and (min-width: 1050px) {
	.header__info {
		display: none;
	}
	.header__image {
		position: relative;
		.container__box{
			display: flex;
			margin-top: auto;
			margin-bottom: auto;
		img {
			margin-left: auto;
			
		}

		}
		
		.list__image {
			margin-right: auto;
			font-style: normal;
			font-weight: 700;
			font-size: d-font(16,24,$min_width,$max_width);;
			line-height: d-font(29,33,$min_width,$max_width);;
			color: $greytext;
			display: block;
			align-self: center;
			text-align: left; 
			p {
				margin-left: 10px;
				margin-bottom: 10px;
				padding-left: 45px;
				background: url("../img/ok.svg") left center no-repeat; 
			
			}
		}
		.hidden__text__image {
			display: block;
			position: absolute;
			width: 100%;
			bottom: 20px;
			.content__text__image{
				display: flex;
				height: 95%;
				justify-content: space-between;
				align-items: flex-end;
				.shadow__hidden {
					filter: drop-shadow(-1px 6px 3px rgba(50, 50, 0, 0.5));
					font-weight: 300;
					.program__image{
						width: 400px;
						height: 70px;
						display: flex;
						flex-direction: column;
						justify-content: center;
						align-self: flex-start;
						clip-path: polygon(0% 0%, calc(100% - 35px) 0%, 100% 100%, 0% 100%);
						background: #F3F6FC;
						box-shadow: 9px 6px 6px rgba(0, 0, 0, 0.25);
						padding-left: 60px;
						font-style: normal;
						font-size: px-to-rem(18px);
						line-height: px-to-rem(18px);
						color: $blue;
						text-transform: uppercase;
						box-shadow: 9px 6px 6px rgba(0, 0, 0, 0.25);
						p {
							margin-left: auto;
							margin-right: 80px;
						}
					}
					.avtor__image {
						width: 400px;
						height: 70px;
						display: flex;
						flex-direction: column;
						justify-content: center;
						align-self: flex-end;
						clip-path: polygon(35px 0%, 100% 0%, 100% 100%, 0% 100%);
						background: #F3F6FC;
						padding-left: 60px;
						padding-top: auto;
						padding-bottom: auto;
						font-style: normal;
						font-size: px-to-rem(18px);
						line-height: px-to-rem(20px);
						color: $blue;
						text-transform: uppercase;
						text-align: left;
						p {
							&:nth-child(1) {
								font-weight: 300;
								
							}
							&:nth-child(2) {
								font-weight: 800;
								
							}
						}
					}
				}
			}

		}
	}
}
