.block__forwho{
	display: block;
	.hidden__image{
		display: none;
	}
}
.header__forwho{
	display: flex;
	// height: 91px;
	justify-content: center;
	padding-top: 20px;
	// padding-bottom: 20px;
	
	text-align: center;
	background: linear-gradient(180deg, #D5D5D5 0%, rgba(213, 213, 213, 0) 100%);
	h3{
	font-style: normal;
	font-weight: bold;
	font-size: d-font(18,36,$min_width,$max_width);
	line-height: 120%;
	display: flex;
	align-items: center;
	text-align: center;
	letter-spacing: -0.015em;
	text-transform: uppercase;
	color: #0059A5;}
}

.item__forwho {
	display: flex;
	flex-direction: column;
	align-items:flex-end;
	.title__forwho {
		width: 90% !important;
	padding: 14px;
	font-style: normal;
	font-weight: bold;
	font-size: 12px;
	line-height: 12px;
	text-align: center;
	align-self: center;
	color: #757575;
	}
	&:nth-child(1){
		.num__forwho {
			background: #0059A5;
		}
		.info__forwho {
			background: linear-gradient(90deg, #006BC7 2.24%, rgba(0, 107, 199, 0.957704) 59.78%, #006BC7 94.4%);
		}
	}
	&:nth-child(2){
		.num__forwho {
			background: #CC3254;
		}
		.info__forwho {
			background: linear-gradient(90deg, #E33A59 0%, rgba(227, 58, 89, 0.981806) 46.59%, #E33A59 94.7%);
		}
	}
	&:nth-child(3){
		.num__forwho {
			background: #F2B318;
		}
		.info__forwho {
			background: linear-gradient(90deg, #FEC213 0%, rgba(254, 194, 19, 0.905403) 66.12%, #FEC213 100%);
		}
	}
	&:nth-child(4){
		.num__forwho {
			background: #259390;
		}
		.info__forwho {
			background: linear-gradient(90deg, #46AEAF 0%, rgba(70, 174, 175, 0.88076) 74.45%, #46AEAF 94.65%);
		}
	}
	&:nth-child(5){
		.num__forwho {
			background: #433485;
		}
		.info__forwho {
			background: linear-gradient(90deg, #604A95 0.86%, rgba(96, 74, 149, 0.956356) 70.6%, #604A95 100%);
		}
	}
	&:nth-child(6){
		.num__forwho {
			background: #EA7D18;
		}
		.info__forwho {
			background: linear-gradient(90deg, #F8931F 0.86%, rgba(248, 147, 31, 0.95959) 70.92%, #F8931F 100%);
		}
	}
	.info__forwho {
		display: flex;
		min-height: 85px;
		width: 97%;
		.num__forwho {
			display: flex;
			min-width: 100px;
			
			box-shadow: 3px 3px 10px rgba(0, 0, 0, 0.5);
			justify-content: center;
			align-items: center;
			p {
				font-style: normal;
				font-weight: bold;
				font-size: 36px;
				line-height: 40px;
				text-transform: uppercase;
				font-feature-settings: 'tnum' on, 'lnum' on;
				color: #FFFFFF;
			}
		}
		.text__forwho{
			padding: 2px 4px 2px 10px;
			font-style: normal;
			font-weight: normal;
			font-size: 12px;
			line-height: 130%;
			display: flex;
			align-items: center;
			letter-spacing: -0.015em;
			font-feature-settings: 'pnum' on, 'lnum' on;
			color: #FFFFFF;
		}
	}
	
}
@media screen and (min-width: 1150px) {
	.block__forwho{
		
		.hidden__image{
			margin-left: 10%;
			display: block;
			width: 50%;
			margin-right: 2.5%;
			text-align: center;
			align-self: center;
			img {
				width: auto;
				height: 100%;
				max-height: 830px;
			}
		}
		.content__forwho{
