.works_block{
	width: 100%;
	.image_big_block{
		width: 100%;
		height: 244px;
		background-image: url("../img/big_lada.png");
		background-repeat: no-repeat;
		background-position: right 30% center;
		background-size: cover;
	}
	.hidden_text_works {
		display: none;
	}
	.text_works{
		width: 80%;
		margin-left: auto;
		margin-right: auto;
		align-self: center;
		padding: 0 15px 15px 10px;
		font-style: normal;
		font-weight: bold;
		font-size: d-font(24,36,$min_width,$max_width);
		line-height: 130%;
		text-align: center;
		text-transform: uppercase;
		h3{
			color: #6959B6;
		}
		p{
			font-size: d-font(16,25,$min_width,$max_width);
			line-height: d-font(22,39,$min_width,$max_width);
			font-weight: normal;
		}
	}
}

@media screen and (min-width: 690px) {
	.works_block{
		.image_big_block{
			height: calc(244px + 30vw) !important;
		}
	}
}

@media screen and (min-width: 1300px) {
	.works_block{
		.image_big_block{
			position: relative;
			.hidden_text_works{
				width: 30%;
				max-width: 25%;
				top: 20%;
				// left: calc(20px + 5vw);
				display: block;
				position: absolute;
				font-style: normal;
				font-weight: bold;
				font-size: d-font(24,36,$min_width,$max_width) !important;
				line-height: 130%;
				text-align: left;
				text-transform: uppercase;
				margin-left: 10%;
				h3{
					color: #6959B6;
				}
				p{
					font-size: 18px;
					line-height: 130%;
					font-weight: normal;
				}
			}
		}
		
		.text_works{
			display: none;
		}
}
}