.bonus_block {
	.group__block_install {
		display: flex;
		flex-direction: column;
		align-items: center;
	.get_bonus {
		display: flex;
		flex-direction: column;
		align-items: center;
		width: 80%;
		max-width: 330px;
		margin-left: auto;
		margin-right: auto;
		.title_get {
		display: flex;
		align-item: center;
		text-align: center;
		justify-content: center;
		padding-bottom: 8px;
		p {
			font-size: d-font(14,24,$min_width,$max_width);
			font-style: normal;
			font-weight: bold;
			line-height: 120%;
			color: #0059A5;
		}
		}
		.content_get__bonus {
			margin: 0 auto 0 auto;
			width:80%;
			display: flex;
			justify-content: space-around;
			img {
				width: d-font(114,228,$min_width,$max_width);
				height: d-font(99,198,$min_width,$max_width);
			}
		}
		a {
			margin-top: 15px;
			font-style: normal;
			font-weight: normal;
			font-size: d-font(12,18,$min_width,$max_width);
			line-height: 100%;
			color: #0059A5;
			text-align: center;
		}
	}
	.full_bonus {
		display: flex;
		flex-direction: column;
		align-items: center;
		padding-top: 22px;
		width: 80%;
		max-width: 330px;
		margin-left: auto;
		margin-right: auto;
		.title_full {
			display: flex;
			align-item: center;
			text-align: center;
			justify-content: center;
			padding-bottom: 8px;
		}
		p {
			font-size:d-font(14,24,$min_width,$max_width);
			font-style: normal;
			font-weight: bold;
			line-height: 110%;
			color: #0059A5;
		}
		.content_full_bonus {
			width: 50%;
			display: flex;
			justify-content: space-around;
			margin-bottom: 8px;
			img {
				width: d-font(114,228,$min_width,$max_width);
				height: d-font(99,198,$min_width,$max_width);
			}
		}
		a {
			margin-top: 15px;
			font-style: normal;
			font-weight: normal;
			font-size: d-font(12,18,$min_width,$max_width);
			line-height: 100%;
			color: #0059A5;
		}
	}
}
	.installment_block {
		margin-top: 30px;
		display: flex;
		flex-direction: column;
		align-items: center;
		p {
			font-size: d-font(18,36,$min_width,$max_width) !important;
		}
		.image_install {
			width: 80%;
			img {
				width: 100%;
			}
		}
		button {
				
				margin: 13px auto 14px auto;
				display: block;
				align-items: center;
				justify-content: center;
				font-size: d-font(16,24,$min_width,$max_width);
				background: #6959B6;
				border-radius: 30px;
				box-sizing: border-box;
				box-shadow: none;
				min-height: 55px;
				width: d-font(272,320,$min_width,$max_width);
				color: white;
				font-weight: bold;
			}
		
		.title_install {
			width: 80%;
			display: flex;
			align-item: center;
			text-align: center;
			justify-content: center;
			padding-bottom: 8px;
			p {
				font-size: d-font(14,30,$min_width,$max_width);
				font-style: normal;
				font-weight: bold;
				line-height: 110%;
				color: #0059A5;
			}
		}
		.description_install {
			width: 90%;
			text-align: center;
			margin-top: 10px;
			padding-bottom: 8px;

			p {
				font-size: d-font(12,18,$min_width,$max_width) !important;
				font-style: normal;
				font-weight: normal;
				line-height: 120%;
				color: #000000;
				margin-bottom: 10px;
			}
		}
		a {
			width: 100%;
			justify-content: center;
			font-style: normal;
			font-weight: bold;
			font-size: 18px;
			line-height: 20px;
			display: flex;
			align-items: center;
			text-align: center;
			text-transform: uppercase;
			color: #6959B6;
			margin-bottom: 10px;
		}
	}
	
		
}
@media screen and (min-width: 800px) {
	.price_block{
		background: linear-gradient(304.65deg, #EBEBED 39.31%, rgba(235, 235, 237, 0) 74.68%);
		padding-bottom: 40px;
	}
	.bonus_block{
		height: auto;
		padding: 3% 10% 5% 10%;
		display: flex;
		flex-direction: row;
		align-items: flex-start;
		flex-wrap: wrap;
		// justify-content: space-between;
		background-image: url('../img/girl_install.png');
		background-repeat: no-repeat;
		background-position: bottom right 15%;
		background-size: d-font(256,552,$min_width,$max_width) !important; 
		.group__block_install {
			display: flex;
			flex-direction: row;
			align-items: flex-start;
			width: 100% !important;
			// justify-content: space-evenly;
			.full_bonus {
				padding-top: 0;
			} 
		}
		
	}
	
	
	
	.image_install{
		display: none;
	}
	.installment_block {
		align-self: flex-end;
		width: 100%;
		align-items: flex-start !important;
		margin-top: 10% !important;
		
		.title_install {
			p { width: 100%;}
			width: 100% !important;
			text-align: left !important;
		}
		p {
			text-align: left;
		}
		.title_install {
			width: 50% !important;
		}
		button {
			margin: 13px auto 14px 0 !important;
		}
		a {
			display: none !important;
		}
	}
}