.block_after_traning{
	margin-top: 30px;
	margin-bottom: 50px;
	.title_traning{
		width: 100%;
		padding-top: 20px;
		padding-bottom: 20px;
		display: flex;
		align-items: center;
		text-align: center;
		text-transform: uppercase;
		justify-content: center;
		
		background: -webkit-linear-gradient(#D5D5D5 0%, rgba(213, 213, 213, 0) 100%);
		background: -o-linear-gradient(#D5D5D5 0%, rgba(213, 213, 213, 0) 100%);
		background: linear-gradient(#D5D5D5 0%, rgba(213, 213, 213, 0) 100%);
		h3{
			
			color: #0059A5;
			font-style: normal;
			font-weight: bold;
			font-size: d-font(18,36,$min_width,$max_width);
			line-height: 120%;
		} 
	}
	.sertificate_block {
		display: flex;
		flex-direction: column;
		align-items: center;
		text-align: center;
		padding-bottom: 10px;
		
	}
	.button_sertificate{
			button {
				margin-left: auto;
				margin-right:auto;
			box-sizing: border-box;
			display: flex;
			align-items: center;
			justify-content: center;
			font-size: px-to-rem(16px);
			background: #6959B6;
			box-shadow: 3px 3px 10px rgba(0,0,0,.5)!important;
			// border-radius: 10px;
			min-height: 50px;
			min-width: 250px;
			color: white;
			text-transform: uppercase;
			}
		}
	.sertificate{
		.title_sertificate {

			}
		box-sizing: border-box;
		padding: 20px 20px 31px d-font(20,35,$min_width,$max_width);
		display: flex;
		flex-direction: column;
		color: white;
		align-items: center;
		text-align: center;
		background: #6959B6;
		border-radius: 5px 5px 0px 80px;
		width: d-font(300,450,$min_width,$max_width) !important;
		h3{
			margin-top: 0px;
			font-style: normal;
			font-weight: bold;
			font-size: px-to-rem(18px);
			line-height: 180%;
			text-transform: uppercase;
		}
		img{
			width: 270px;
			height: auto;
		}
		.sertificate_footer {
			width: 80%;
		}
		h4{
			font-style: normal;
			font-weight: bold;
			font-size: d-font(11,18,$min_width,$max_width);
			line-height: 130%;
			letter-spacing: -0.015em;
		}
		p{
			font-style: normal;
			font-weight: normal;
			font-size: d-font(11,18,$min_width,$max_width);
			line-height: 130%;
			letter-spacing: -0.015em;
		}
		
		
	}
	.student_block {
		width: 100%;
		.column_block{
			width: 100%;
		padding-top: 20px;
		display: flex;
		flex-direction: row;
		justify-content: space-around;
		align-self: stretch;
		.hidden_text_block{
			display: none;
		}
			.student_card {
				padding: 5px;
				border-radius: 10px;
				
				display: flex;
				flex-direction: column;
				align-items: center;
				text-align: center;
				min-width: 80px;
				width: 120px;
				margin-bottom: 20px;
				box-sizing: border-box;
				.photo_student {
					width: d-font(76,235,$min_width,$max_width);
					height: d-font(76,235,$min_width,$max_width);
					box-sizing: border-box;
					img {
						width: 100%;
					}
				}
				.student_info{
					font-style: normal;
					margin-bottom: 10%;
					line-height: calc(14px + (10 + 10 * 0.7) * ((100vw - 320px) / 1680));

					h5{
						font-weight: bold;
						margin-bottom: 5px;
						margin-top: 5px;
						font-size: calc(14px + (10 + 10 * 0.7) * ((100vw - 320px) / 1680));
					}
					p{
						font-weight: normal;
						font-size: calc(12px + (8 + 8 * 0.7) * ((100vw - 320px) / 1680));
						line-height: calc(12px + (8 + 8 * 0.7) * ((100vw - 320px) / 1680));
					}
				}
				button {
					margin: 5%;
					margin-top: auto;
					width: 100%;
					height: 30px;
					font-size: 12px;
					font-weight: bold;
					text-transform: uppercase;
				}
				
				
			}
			
			
		}
	}
}

@media screen and (min-width: 670px) {
	.sertificate_block {
		display: flex;
		flex-direction: row-reverse!important;
		align-items: flex-start !important;
		justify-content: space-around;
		
	}

	.student_block {
		.column_block{
		padding-top: 0 !important;
		width: auto;
		display: flex;
		align-items: stretch!important;
		flex-wrap: wrap;
		.student_card {
			width: 250px !important;
			button {
				font-size: 18px !important;
				height: 45px !important;
			}
		}
		}
	}
	.button_sertificate {
		display: none;
		width: 100%;
	}
}
/* Вставка разрыва строки между двумя flex-элементами заставит 
 * flex-элемент, который расположен после него переноситься на новую строку */


@media screen and (min-width: 1328px) {
	.block_after_traning .container__div {
		width: 90%;
		margin-left: auto;
		margin-right: auto;
		padding-bottom: 25px;
		background: #EEEEF0;
		border: 1px solid #6959B6;
		box-sizing: border-box;
		box-shadow: 4px 4px 10px rgba(0, 0, 0, 0.25);
		border-radius: 10px;
	}
	.student_block {
		.column_block{		
		padding-top: 0 !important;
		align-items: flex-start;
		width: 100%;
		display: flex;
		flex-wrap: wrap;
		flex-direction: row!important;
		align-self: flex-start!important;
		.student_card{
			margin-top: 25px;
			padding-top: 25px;
			align-items: center;
			width: 250px !important;
			button {
				display: none;
			}
		}

		.hidden_text_block{
			width: 100%;
			display: block !important;
			.arrow__block {
				width: 100%;
				display: flex !important;
				flex-direction: row !important; 
				align-items: center;
				justify-content: space-around;
				.arrow {
					width: 250px;
					img {
						width: 57px;
						height: 57px;
					}
				}
			}
			h3 {
			text-align: center;

			font-size: 18px;
			line-height: 20px;
			font-weight: 800;
			letter-spacing: -0.015em;
			text-transform: uppercase;
			color: #000000;
			}
			.text__blocks_group {
				display: flex;
				flex-direction: row;
				align-items: stretch;
				width: 100%;

				justify-content: space-around !important;
				.text__group {
					
					width: 250px;
					font-style: normal;
					font-weight: 300;
					font-size: 20px;
					line-height: 111%;
					/* or 111% */
					text-align: left;
					letter-spacing: -0.015em;
					color: #000000;
					p {
						margin-bottom: 10px;
					}
				}
			}
		}

		}
	}
	.button_sertificate{
			display: none;
		}
	.sertificate_block{
		.sertificate{
			width: 354px;
			max-height: 722px; 
			align-items: flex-start !important;
		}
	}
	
}

@media screen and (min-width: 1200px) {
	.block_after_traning .container__div {
		width: 80%;
	}
	.student_card {
			.photo_student {
					width: 160px !important;
					height: 160px !important;
					
					}
		}
	.sertificate_block{
		.sertificate{
			text-align: left;
			width: 354px;
			height: auto;
			padding-bottom: 100px; 
		}
	}
	
}
@media screen and (min-width: 1650px) {
	.block_after_traning{
		.container__div{
			width: 80vw;
			margin-left: auto;
			margin-right: auto;
		}
	}
		
}