.guarant {
	width: 100%;
	.image_guarant {
		width: 100%;
		img {
			width: 100%;
		}
	}
	.title_guarant {
		h1 {
			margin: 0 0 10px 0;
			padding-top: 34px;
			font-style: normal;
			font-weight: bold;
			font-size: 36px;
			line-height: 100%;
			text-align: center;
			text-transform: uppercase;
			color: #6959B6;
		}
	}
	.sub_title_guarant{
		h3 {
			margin: 0;
			font-style: normal;
			font-weight: bold;
			font-size: d-font(18,24,$min_width,$max_width);
			line-height: 110%;
			display: flex;
			align-items: center;
			text-align: center;
			color: #0059A5;
		}
	}
	.description_guarant {
		padding: 9px 28px 51px 28px;
		p {
			font-style: normal;
			font-weight: normal;
			font-size: d-font(14,18,$min_width,$max_width);
			line-height: 130%;
			display: flex;
			align-items: center;
			text-align: center;
			color: #0059A5;
		}
	}
}
@media screen and (min-width: 500px) {
	.guarant {
		display: flex;
		flex-direction: column;
		align-items: center;
	.image_guarant {
		width: 100%;
		img {
			display: none;
		}
		background-image: url('../img/guarant_bg.png');
		background-repeat: no-repeat;
		background-position: center center;
		background-size: cover;
		height: d-font(269,789,$min_width,$max_width);
	}
	.text__block_guarant {
		width: 80%;
		margin-top: -35px;
		display: flex;
		flex-direction: column;
		align-items: center;
		.title_guarant {
		h1 {
			margin-bottom: 10px !important;
		}
	}
		h3 {
			width: 100%;
			margin-bottom: 10px;
		}
		p {
			width: 80%;
			margin-left: auto;
			margin-right: auto;
		}
	}
	.title_guarant {
		h1 { 
			margin: 0 !important;
			padding: 0 !important;}

	}
}

}

