.footer__block {
	margin-top: 3%;
	width: 100%;
	display: flex;
	flex-direction: column;
	align-items: center;
	.logotype_block {
		width: 100%;
		text-align: center;
		img {
			min-width: 283px;
		}
	}
	hr {
		height: 0.3px;
		width: 90%;
		background: rgba(0, 0, 0, 0.5);
	}
	.description_footer {
		font-style: normal;
		font-weight: normal;
		font-size: 14px;
		line-height: 130%;
		display: flex;
		flex-direction: column;
		align-items: flex-start;
		
		margin-top: 21px;
		a {
			color: #595959;
		}
		p {
			margin-bottom: 28px;

		}
	}
	.social_block {
		display: flex;
		flex-direction: column;
		align-items: center;
		margin-bottom: 30px;
		.social_icon {
			width: 100%;
			display: flex;
			justify-content: space-between;
			margin-bottom: 10px;
		}

	}
	.social_icon {
		img {
			max-width: 43px;
		}
		
	}
	.footer_permission {
		width: 100%;
		text-align: center;
		margin-bottom: 30px;
		font-size: d-font(16,25,$min_width,$max_width);
		color: #595959;
	}
}
@media screen and (min-width: 800px) {
	.description_footer {
		width: 80%;
		display: flex;
		flex-direction: row !important;
		// justify-content: space-around;
		justify-content: space-evenly;
	}
}