.process_block {
	background: linear-gradient(172.33deg, #EBEBED -3.93%, rgba(235, 235, 237, 0.703334) 13.81%, rgba(235, 235, 237, 0.360002) 87.7%, rgba(235, 235, 237, 0) 93.12%, rgba(235, 235, 237, 0) 98.9%);
	
	.title_process {
		width: 100%;
		margin-left: auto;
		margin-right: auto;
		padding-top: 20px;
		padding-bottom: 10px;
		display: flex;
		align-items: center;
		text-align: center;
		text-transform: uppercase;
		justify-content: center;
		h3 {
			margin-bottom: 0;
			width: 80%;
			color: #0059A5;
				font-style: normal;
				font-weight: bold;
				font-size: d-font(18,36,$min_width,$max_width);
				line-height: 120%;
		}
	}
	.sub_title_process {
			
			font-style: normal;
			font-weight: normal;
			font-size: d-font(14,36,$min_width,$max_width);
			line-height: 130%;
			text-align: center;
			letter-spacing: -0.015em;
			text-transform: uppercase;
			font-feature-settings: 'tnum' on, 'lnum' on;
			color: #6959B6;
			h4 {
				margin-top: 0;
			}
		}
	.info_process_block {
		padding-top: 2%;
		display: flex;
		flex-direction: column;
		background-image: url('../img/spring.png');
		background-repeat: no-repeat;
		background-size: contain;
		padding-bottom: 5%;
		
		.card_process {
			display: flex;
			flex-direction: row;
			width: 81%;
			margin-bottom: 30px;
			margin-left: 10%;
			margin-right: 10%;
			.icon_process {
				
				max-width: 100px;
				img {
					width: calc(50px + 2vw);
				}
			}
			.description_process {
				margin-left: 26px;
				margin-right: 30px;
				h4{
				margin: 0;
				font-weight: bold;
				font-size: d-font(11,20,$min_width,$max_width);
				line-height: 130%;
				margin-bottom: 10px;
				}
				p{
					font-weight: normal;
					font-size: d-font(11,20,$min_width,$max_width);
					line-height: 130%;
				}
			}
			
		}
	}
}

@media screen and (max-width: 400px) {
	.card_process {
			display: flex;
			flex-direction: row;
			width: auto !important;
			margin-bottom: 30px;
			margin-left: 20% !important;
			margin-right: 0 !important;
		}
}